.document-update-form {
  .field {
    margin-bottom: var(--sp-2);

    label {
      display: block;
      padding-bottom: var(--sp-1);
    }
  }
}

.document-update-view {
  .document-title {
    line-height: 22px;
    border: 1px solid transparent;
    background-color: var(--gray-50, transparent);

    &:focus {
      background-color: white;
    }

    &[value=''] {
      background-color: var(--gray-500, transparent);
    }
  }

  .document-field {
    input[value=''],
    textarea[value=''] {
      &:not(:focus) {
        border-color: transparent;
        background-color: var(--gray-100, transparent);
      }
    }
  }
}

.document-update-view-actions {
  display: flex;
  align-items: center;
}
