.docufile {
    &-list {
        .docufile-btn {
            .icon {
                width: 32px;
                height: 32px;
            }
        }

        .docufile-icon-wrapper {
            min-width: 64px;
            display: flex;
            justify-content: center;
        }
    }
    &-grid {
        .docufile-btn {
            // padding: var(--sp-1);
            border-color: transparent;
            display: flex;
            flex-direction: column;
            align-items: center;

            &:hover {
                background-color: var(--blue-50);
            }

            .icon {
                width: 32px;
                height: 32px;
            }

            img {
                border-radius: var(--border-radius, 6px);
            }

            .label {
                // max-width: 200px;
                // width: 100%;
                width: 125px;
                word-wrap: break-word;
            }

            .size {
                color: var(--gray-600);
            }
        }
    }
}
